	module.exports = {"pathContext":{"frontmatter":{"path":"/docs/aspect-ratio","title":"Aspect Ratio","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Aspect Ratio</h2>\n<p>AspectRatio is a property introduced by Yoga and is not present as a settable\nproperty in the css flexbox specification. Flexbox does has the notion of\naspect ratio though for things with intrinsic aspect ratio such as images.</p>\n<p>The <code>aspect ratio</code> property in Yoga has the following properties:</p>\n<ul>\n<li>Accepts any floating point value > 0, the default is undefined.</li>\n<li>Defined as the ratio between the <code>width</code> and the <code>height</code> of a node e.g. if a node has an aspect ratio of 2 then its <code>width</code> is twice the size of its <code>height</code>.</li>\n<li>Respects the <code>min</code> and <code>max</code> dimensions of an item.</li>\n<li>Has higher priority than <code>flex grow</code></li>\n<li>If <code>aspect ratio</code>, <code>width</code>, and <code>height</code> are set then the cross axis dimension is overridden.</li>\n</ul>\n<p><controls prop=\"aspectRatio\"></controls></p>"}}

